﻿.lists {
	border-radius: 10px;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
	display: flex;
	flex-flow: row nowrap;
	overflow: hidden;
}

.list {
	border-radius: 10px;
	display: flex;
	flex-flow: column nowrap;
	flex-grow: 1;
	margin: 0;
	padding: 1em;

	li {
		line-height: 1.5rem;
		list-style: none;

		a {
			line-height: 1rem;
		}
	}

	&-horizontal {
		flex-flow: row wrap;
	}
}

.lists {
	.list {
		border-radius: 0;
		box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
	}
}
